import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import find from '@/pages/find'
import home from '@/pages/home'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/find',
      component: find
    },
    {
      path: '/home',
      component:home

    }


  ]
})



